<template>
    <div>
      <label for="speechInput">üó£ Parle, je t'√©coute :</label>
      <input id="speechInput" class="white-input" type="text" v-model="text" readonly />
      <button @click="toggleRecording" :disabled="!recognition">üé§</button>
      <p v-if="!recognition" style="color: red;">üö® Votre navigateur ne supporte pas la reconnaissance vocale.</p>
    </div>
  </template>
  
  <script>
  export default {
    data() {
      return {
        text: "",
        recognition: null,
        isRecording: false
      };
    },
    mounted() {
      if (window.SpeechRecognition || window.webkitSpeechRecognition) {
        this.recognition = new (window.SpeechRecognition || window.webkitSpeechRecognition)();
        this.recognition.continuous = false;
        this.recognition.interimResults = false;
        this.recognition.lang = "fr-FR";
        this.recognition.onresult = (event) => {
          this.text = event.results[0][0].transcript;
        };
      }
    },
    methods: {
      toggleRecording() {
        if (!this.recognition) {
          alert("Votre navigateur ne supporte pas la reconnaissance vocale.");
          return;
        }
        if (this.isRecording) {
          this.recognition.stop();
        } else {
          this.recognition.start();
        }
        this.isRecording = !this.isRecording;
      }
    }
  };
  </script>
  