<template>
    <div>
        <NotificationService ref="notification" />
      <button @click="fetchData">üì° Appeler API</button>
      <p v-if="data">Donn√©es : {{ data }}</p>
    </div>
  </template>
  
  <script>
  import NotificationService from "./NotificationService.vue";
  
  export default {
    components: { NotificationService },
    data() {
      return {
        data: null,
        queue: []
      };
    },
    methods: {
      async fetchData() {
        if (!navigator.onLine) {
          alert("Pas de connexion. La requ√™te sera envoy√©e plus tard.");
          this.queue.push("https://jsonplaceholder.typicode.com/todos/1");
          return;
        }
        try {
          const response = await fetch("https://jsonplaceholder.typicode.com/todos/1");
          const result = await response.json();
          this.data = result.title;
        } catch (error) {
          console.error("Erreur API", error);
        }
      }
    },
    mounted() {
      window.addEventListener("online", async () => {
        while (this.queue.length > 0) {
          await this.fetchData();
          this.queue.shift();
        }
        this.$refs.notification.sendNotification("‚úÖ API R√©ponse !", "Les donn√©es ont √©t√© r√©cup√©r√©es avec succ√®s.");
      });
    }
  };
  </script>