<template>
    <div>
      <label>Date de rappel :</label>
      <input type="datetime-local" v-model="reminderDate" />
      <button @click="setReminder">‚è∞ D√©finir Rappel</button>
    </div>
  </template>
  
  <script>
  export default {
    name:'ReminderAlert',
    data() {
      return {
        reminderDate: ""
      };
    },
    methods: {
      setReminder() {
        if (!this.reminderDate) return;
        const timeDiff = new Date(this.reminderDate) - new Date();
        if (timeDiff > 0) {
          setTimeout(() => {
            new Notification("üìÖ Rappel !", {
              body: "C'est l'heure de votre √©v√©nement !"
            });
          }, timeDiff);
          alert("Rappel programm√© avec succ√®s !");
        } else {
          alert("Choisissez une date future !");
        }
      }
    }
  };
  </script>